@font-face {
  font-family: Avenir;
  font-weight: 300;
  font-style: normal;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: 900;
  font-style: normal;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

* {
  box-sizing: border-box;
}

.body {
  color: #3d4e61;
  font-family: Avenir, sans-serif;
}

.input {
  color: #3d4e61;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
  display: block;
  box-shadow: 0 1px 8px #3d4e611a;
}

.input__big {
  background-position: 26px;
  background-size: 19px 19px;
  height: 70px;
  padding-left: 62px;
  font-family: Avenir, sans-serif;
  font-size: 16px;
}

.input__small {
  background-position: 12px 14px;
  background-size: 11px 11px;
  height: 42px;
  padding-left: 33px;
  font-family: Avenir, sans-serif;
  font-size: 14px;
}

.input__big::placeholder, .input__small::placeholder {
  font-family: Avenir, sans-serif;
  font-weight: 400;
}

.input__big:hover {
  box-shadow: 0 4px 4px #00000040;
}

.input__small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.input__big:focus {
  color: #3d4e61;
  text-shadow: 1px 4px 4px #00000040;
  outline: none;
  font-weight: 900;
  box-shadow: 0 4px 4px #00000040;
}

.input__small:focus {
  color: #3d4e61;
  text-shadow: none;
  outline: none;
  font-weight: 900;
}
